import React, { Component } from 'react';
var countries = require('./icons/countries');
var finalResult = require('./result');

class Participants extends Component {
    constructor(props) {
        super(props);
        this.state = {
          displayBracket: false,
          totalPoints: 'placeholder for points'
        }
        this.calculatePoints();
      }

      calculatePoints() {
        console.log(finalResult);
      }
    
      toggleBracket = () => {
        this.setState({
          displayBracket : !this.state.displayBracket
        });
      }
    render () {
        return (
            <div className='participants'>
                <div className='header' onClick={this.toggleBracket}>
                    <div className='name'>{this.props.participant.name}</div>
                    <div className='location'>{this.props.participant.location}</div> 
                    <div className='points'>{this.state.totalPoints}</div>
                </div>
            { this.state.displayBracket && <div className="brackets">
                  <div className={`r16 rows`}>
                    <h1>Round of 16</h1>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r1)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r2)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r3)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r4)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r5)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r6)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r7)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r8)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r9)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r10)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r11)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r12)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r13)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r14)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r15)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.r16.r16)} />
                    </div>
                  </div>
                  <div className='quarter'>
                    <h1>Quarter Final</h1>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q1)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q2)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q3)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q4)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q5)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q6)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q7)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.quarter.q8)} />
                    </div>
                  </div>
                  <div className='semi'>
                    <h1>Semi Final</h1>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s1)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s2)} />
                    </div>
                    <div>
                      <img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s3)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.semi.s4)} />
                    </div>
                  </div>
                  <div className='final'>
                    <h1>Final</h1>
                      <div>
                          <img className="App-logo" src={countries.default.get(this.props.participant.brackets.final.f1)} /> vs <img className="App-logo" src={countries.default.get(this.props.participant.brackets.final.f2)} />
                      </div>
                  </div>
                  <div className='winner'>
                    <h1>Winner</h1>
                    <img className="App-logo" src={countries.default.get(this.props.participant.brackets.winner)} />
                  </div>
              </div>
              }
          </div>
        );
    }
}
 export default Participants;


